@media screen and (max-width: 820px) {
  .cart {
    grid-template-columns: 1fr;
    > aside {
      padding: 2rem;
      border-radius: 0%;
      > h2 {
        margin: 1rem;
        font: 100 1.2rem "Roboto";
        letter-spacing: 2px;

        &:last-of-type {
          font-weight: 600;
        }
      }
    }
  }
}
